
<template>
  <div>
    <h4 class="font-weight-bold py-3 mb-0">首頁</h4>
    <b-list-group>
      <b-list-group-item v-for="(form, formIndex) in formListRows" :key="'form_'+formIndex">
        <b-row>
          <b-col>
            <div><small>{{ form.start_time }} - {{ form.end_time }}</small></div>
            <span>{{ form.title }}</span>
          </b-col>
          <b-col offset="1" cols="4">
            <button type="button" @click="editForm(form)" class="ml-auto mx-1 btn btn-round btn-outline-secondary">編輯</button>
            <button type="button" @click="showOrders(form)" class="mx-1 btn btn-round btn-outline-secondary">
              訂單明細
              <b-badge variant="primary" pill v-if="form.orders">{{ form.orders.length }}</b-badge>
            </button>
          </b-col>
        </b-row>
      </b-list-group-item>
    </b-list-group>
    
    <Loading :isLoading="isLoading"/>
  </div>
</template>

<script>
import moment from 'moment'
import Loading from '@/views/Loading'
import { getSettingDoc } from '@/gapi/getSettingDoc';
import { getOrderDoc } from '@/gapi/getOrderDoc';

export default {
  name: 'NewForm',
  data () {
    return {
      isLoading: true,
      formListRows: [],
    }
  },
  created() {
    this.accessSpreadSheet();
  },
  components: {
    Loading,
  },
  methods:{
    async accessSpreadSheet() {
      const adminDoc = await getSettingDoc();
      const formListSheet = adminDoc.sheetsByTitle['表單列表'];
      this.formListRows = await formListSheet.getRows();
      const orderDoc = await getOrderDoc();
      
      for (let index = 0; index < this.formListRows.length; index++) {
        const form = this.formListRows[index];

        const sheet = orderDoc.sheetsByTitle[form.sheetTitle];
        form.orders = await sheet.getRows();
      }
      this.isLoading = false;
    },
    editForm: function(form) {
      this.$router.push(`form-list/${form.sheetTitle}`)
    },
    showOrders: function(form) {
      this.$router.push(`order-analysis/${form.sheetTitle}`)
    },
  }
}
</script>